.app-header {
  display: flex;
  justify-content: space-between;
  height: 50px;
  padding: 20px;
}

.header-title {
  font-size: 30px;
  color: #fff;
  text-decoration: none;
}

.header-title:focus {
  outline: none;
}
.sidebar-div {
  display: flex;
  padding: 0 20px;
}

.navLink-selected {
  border-radius: 5px;
  background-color: #0078d7;
}

.navLink-selected:nth-of-type(2n) {
  border-radius: 5px;
  background-color: #4CAF50;
}

.navLink-selected:nth-of-type(3n) {
  border-radius: 5px;
  background-color: #d91a14;
}

.sidebar-link {
  color: #fff;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.sidebar-nav a {
  display: block;
  padding: 10px 10px;
  cursor: pointer;
}
.word-plate {
  margin: 20px;
  padding: 10px;
  border: 3px solid #0078d7;
  border-radius: 5px;
  font-size: 20px;
  color: #fff;
  text-transform: capitalize;
  text-align: center;
  background-color: #0078d7;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.word-plate:focus {
  background-color: rgba(0, 120, 215, 0.3);
  outline: none;
}

.word-plate:nth-of-type(2n) {
  border: 3px solid #4CAF50;
  background-color: #4CAF50;
}

.word-plate:nth-of-type(2n):focus {
  background-color: rgba(76, 175, 80, 0.3);
  outline: none;
}

.word-plate:nth-of-type(3n) {
  border: 3px solid #7719aa;
  background-color: #7719aa;
}

.word-plate:nth-of-type(3n):focus {
  background-color: rgba(119, 25, 170, 0.3);
  outline: none;
}

.word-plate:nth-of-type(4n) {
  border: 3px solid #d91a14;
  background-color: #d91a14;
}

.word-plate:nth-of-type(4n):focus {
  background-color: rgba(217, 26, 20, 0.3);
  outline: none;
}

.word-plate:hover {
  border: 3px solid #fff;
}

.word-plate:focus {
  background-color: rgba(0, 120, 215, 0.3);
  outline: none;
}
.div-words-plate {
  display: flex;
  flex-wrap: wrap;
}

.div-words-btn {
  display: flex;
  justify-content: flex-end;
}

.btn-next-words {
  margin: 20px 40px;
  padding: 10px;
  border: 1px solid #4CAF50;
  border-radius: 5px;
  background-color: transparent;
  font-size: 20px;
  color: #4CAF50;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.btn-next-words:hover {
  background-color: #4CAF50;
  color: #fff;
}

.btn-next-words:focus {
  outline: none;
}

.pointer {
  cursor: pointer;
}
.app {
  text-align: left;
}

.app-wrapper {
  display: flex;
  flex-direction: row;
}

.app-main {
  width: 90vw;
}
.scroll-plate {
  margin: 20px;
  padding: 10px;
  border: 3px solid #4CAF50;
  border-radius: 5px;
  position: fixed;
  bottom: 50px;
  font-size: 20px;
  color: #fff;
  text-transform: capitalize;
  text-align: center;
  background-color: #4CAF50;
  cursor: pointer;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.scroll-plate:hover {
  border: 3px solid #fff;
}

.scroll-plate:focus {
  outline: none;
}

.div-input {
  display: flex;
  margin-left: 20px;
  margin-bottom: 150px;
}

.btn-clear {
  padding: 10px;
  border: 1px solid #4CAF50;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: transparent;
  font-size: 20px;
  color: #4CAF50;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.btn-clear:hover {
  background-color: #4CAF50;
  color: #fff;
}

.btn-clear:focus {
  outline: none;
}

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 280px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  max-height: 110px;
  overflow-y: auto;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}
.test-plate {
  margin: 20px;
  padding: 20px;
  font-size: 30px;
  color: #4CAF50;
  text-transform: capitalize;
}

.test-info {
  display: flex;
  justify-content: space-around;
}

.test-score {
  color: #0078d7;
}

.test-lives {
  color: #d91a14;
}

.test-task {
  text-align: center;
}

.test-desc {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.test-input {
  border: none;
  border-bottom: 3px solid #4CAF50;
  background-color: transparent;
  font-size: 30px;
  text-align: center;
  color: #4CAF50;
}

.test-input:focus {
  outline: none;
}

.test-sumup {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: capitalize;
}

.test-sumup-info {
  text-align: center;
}

.test-btn {
  margin: 20px 40px;
  padding: 10px;
  border: 1px solid #4CAF50;
  border-radius: 5px;
  background-color: transparent;
  font-size: 20px;
  color: #4CAF50;
  text-transform: capitalize;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.test-btn:hover {
  background-color: #4CAF50;
  color: #fff;
}

.test-btn:focus {
  outline: none;
}


.not-found-div {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.not-found-sign {
  font-size: 200px;
}

.not-found-link {
  padding: 10px;
  border: 1px solid #4CAF50;
  border-radius: 5px;
  font-size: 20px;
  color: #4CAF50;
  text-decoration: none;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.not-found-link:hover {
  background-color: #4CAF50;
  color: #fff;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-radial-gradient(circle, #575757, #4d4d4d, #434343, #393939, #303030);
  background-image: radial-gradient(circle, #575757, #4d4d4d, #434343, #393939, #303030);
}

.notify-msg {
  font-size: 25px;
  color: #fff;
}
